
import React, { useState } from "react";
import { useEffect } from "react";
import { useRef } from "react";
import LocomotiveScroll from "locomotive-scroll";


// conponets
import CustomCursor from "../../components/CustomCursor";
import Header from "../../components/header/header";
import Main from "../../components/main/Main";
import Portfolio from "../../components/portfolio/Portfolio";
import Gallery from "../../components/gallery/Gallery";
import Footer from "../../components/footer/Footer";
import Awards from "../../components/awards/Awards";
import About from "../../components/about/about";
import GridAnimation from "../../components/grid_animation/GridAnimation";
import Work from "../../components/work/Work";
import AvatarOne from "../../components/avatars/avatar_1/AvatarOne";

// style
import "./home.scss";
import "../../hooks/locomotive-scroll.css";

const Home = ()=>{

    const [preloader, setPreloader] = useState(false);

    useEffect(()=>{
      const fakeDataFetch = () =>{
        setTimeout(() =>{
          setPreloader(false);
        }, 1000);
      };
      fakeDataFetch();
    }, []);

    return(
        <>
          <CustomCursor/>
        {preloader ? ( 
        <div className="loader__wrapper absolute">
            <h2>Q2 studiya</h2>
            <h4>Дизайн интерьера</h4>
        </div> ):(
        <div className="main__container" >
                <Header/>
                <Main/>
                <Portfolio/>
                <About/>
                <Work/>
                <Gallery/>
                <GridAnimation/>
                <Awards/>
                <Footer/>
        </div>
)}
        </>
    )
}
export default Home;