
import React from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";

// swiper
import { Parallax, Keyboard, Mousewheel } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

// style
import "./gallery.scss";
// база данных
import { gallery__info } from "../../assets/gallery__info";
// components


gsap.registerPlugin(ScrollTrigger);
export default function Gallery() {
  
  return (
    <div className="gallery__wrapper">
      <ScrollGallery props={gallery__info}/>
    </div>
  );
}


function ScrollGallery({props}){
  
  return(
    <Swiper
      modules={[ Keyboard, Mousewheel, Parallax ]}
      spaceBetween={0}
      slidesPerView={1}
      mousewheel={{sensitivity:5, releaseOnEdges:true}}
      keyboard={true}
      speed={900}
      parallax={true}
    >
        {props?.map((item) => (
          <SwiperSlide>
            <section className="gallery__item" key={item.id}> 
              <div className="gallery__text">
                <h4 className="gallery__item__progress">{item.number}</h4>
                <h2 className="gallery-info-title" data-swiper-parallax="-500">{item.title}</h2>
                <h3>{item.subtitle_1}</h3>
                <h3>{item.subtitle_2}</h3>
                <h3>{item.subtitle_3}</h3>
              </div>
              <div className="gallery__images">
                <img className="gallery__images__item__1" src={item.disainer} alt="" />
                <img className="gallery__images__item__2" src={item.whatsApp} alt="" />
                <img className="gallery__images__item__3" src={item.whatsApp_2} alt="" />
                <img className="gallery__images__item__4" src={item.video_1} alt="" />
                <img className="gallery__images__item__5" src={item.video_2} alt="" />
                {/* <img className="gallery__images__item__6" src={item.question} alt="" /> */}
                <img className="gallery__images__item__7" src={item.post} alt="" />
                <img className="gallery__images__item__8" src={item.telegram_1} alt="" />
                <img className="gallery__images__item__9" src={item.telegram_2} alt="" />
                <img className="gallery__images__item__10" src={item.email} alt="" />
                <img className="gallery__images__item__11" src={item.kamera} alt="" />
                <img className="gallery__images__item__12" src={item.phone} alt="" />
                <img className="gallery__images__item__13" src={item.builder} alt="" />
                <img className="gallery__images__item__14" src={item.ball_1} alt="" />
                <img className="gallery__images__item__15" src={item.ball_2} alt="" />
                <img className="gallery__images__item__16" src={item.ball_3} alt="" />
                <img className="gallery__images__item__17" src={item.ball_4} alt="" />
                <img className="gallery__images__item__18" src={item.ball_5} alt="" />
                <img className="gallery__images__item__19" src={item.ball_6} alt="" />
                <img className="gallery__images__item__20" src={item.ball_7} alt="" />
              </div>
                
          </section>
          </SwiperSlide>
        ))}
    </Swiper>
  )
}