// styli
import "./main.scss"
// images
import bg_logo from '../../assets/bg_main_logo_img/bg_logo_img.gif'
import sub_bg_1 from "../../assets/bg_main_logo_img/item_10.jpg"
import sub_bg_2 from "../../assets/bg_main_logo_img/item_11.jpg"



function Main() {
    return(
        <>
        <div className="main__bg" data-scroll-section>
            <img src={bg_logo} alt="" />
            <div className="main__subbg">
                <div className="main__subbg__left"><img src={sub_bg_1} alt="" /></div>
                <div className="main__subbg__right"><img src={sub_bg_2} alt="" /></div>
            </div>
        </div>
        
        </>
    )
}

export default Main;