import "./portfolio.scss"
// bibliotek
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";

// images
import portfolio_bg from "../../assets/portfolio/portfolio_bg.png"
// картинки
import portfolio_item_1 from "../../assets/portfolio/выбрано/1.jpg"
import portfolio_item_2 from "../../assets/portfolio/выбрано/2.jpg"
import portfolio_item_3 from "../../assets/portfolio/выбрано/3.jpg"
import portfolio_item_4 from "../../assets/portfolio/выбрано/4.jpg"
import portfolio_item_5 from "../../assets/portfolio/выбрано/5.jpg"
import portfolio_item_6 from "../../assets/portfolio/выбрано/6.jpg"
import portfolio_item_7 from "../../assets/portfolio/выбрано/7.jpg"
import portfolio_item_8 from "../../assets/portfolio/выбрано/8.jpg"
import portfolio_item_9 from "../../assets/portfolio/выбрано/9.jpg"
import portfolio_item_10 from "../../assets/portfolio/выбрано/10.jpg"

gsap.registerPlugin(ScrollTrigger);

function Portfolio() {


// анимация синего фона (шарики)
    const imgRef =useRef(null);
    useEffect(()=>{
        const el = imgRef.current;
        gsap.fromTo(el, {y:0}, {y: "-60%", duration:3, scrollTrigger: {
        trigger: el,
        scrub: 2,
        start: "top bottom",
        end: "top 40%",
        }})
    })
// аниамция большого текста
    const portfolioText =useRef(null);
    useEffect(()=>{
        const el = portfolioText.current;
        gsap.fromTo(el, { y: 0, opacity: 0, scale: 0.5}, { scale:1, opacity: 1, y:-200, duration:3, scrollTrigger: {
        trigger: el,
        scrub:2,
        start: "top bottom",
        end: "top 80%",
        }})
    })
    // анимация синего фона (шарики)
    const imgRefPortfolio =useRef(null);
    useEffect(()=>{
        const el = imgRefPortfolio.current;
        gsap.fromTo(el, {y:0}, {y: "-30%", duration:3, scrollTrigger: {
        trigger: el,
        scrub: 5,
        start: "top bottom",
        end: "top top",
        }})
    })
    return(
        <>
        <div className="portfolio_bg" > 
            <img  className="portfolio_bg_img" src={portfolio_bg} ref={imgRef} alt="" />
            <div className="portfolio_bg_wrapper" >
                <h2 className="font-BeatriceBold" ref={portfolioText}>Gallery</h2>
                <div className="portfolio_bg_grid" ref={imgRefPortfolio}>
                    <div className="portfolio_bg_grid_container">
                        <img src={portfolio_item_1} alt="" />
                        <img src={portfolio_item_2} alt="" />
                        <img src={portfolio_item_3} alt="" />
                        <img src={portfolio_item_4} alt="" />
                        <img src={portfolio_item_5} alt="" />
                        <img src={portfolio_item_6} alt="" />
                        <img src={portfolio_item_7} alt="" />
                        <img src={portfolio_item_8} alt="" />
                        <img src={portfolio_item_9} alt="" />
                        <img src={portfolio_item_10} alt="" />
                    </div>
                </div>
                <button className="portfolio_bg_button">посмотреть все работы =</button>
            </div>
            
        </div>
        </>
    )
}

export default Portfolio;